<template>
  <div>
    <nav-content></nav-content>
    <home-header></home-header>
    <home-banner></home-banner>
    <home-article :article="article"></home-article>
    <statement></statement>
    <home-footer></home-footer>
  </div>
</template>

<script>
import axios from 'axios'
import navContent from '../components/nav'
import homeHeader from './components/Header'
import homeFooter from './components/Footer'
import homeBanner from './components/Banner'
import homeArticle from './components/Article'
import statement from './components/Statement'
export default {
  name: 'Home',
  components: {
    homeHeader,
    homeBanner,
    navContent,
    homeArticle,
    homeFooter,
    statement
  },
  data () {
    return {
      article: []
    }
  },
  methods: {
    getArticleInfo () {
      axios.get('/api/article.json')
        .then(this.articleInfoSucc)
    },
    articleInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.article = data.article
      }
    }
  },
  mounted () {
    this.getArticleInfo()
  }
}
</script>
<style scoped>
</style>



