<template>
  <div>
    <div class="article" id="article">
      <div class="container">
        <div class="row" v-for="item of article" :key="item.id">
          <div class="col-md-8 offset-md-2">
            <div class="row">
              <div class="col-md-4">
                <h3 class="article-title">{{item.title}}</h3>
                <p class="article-time">{{item.time}} | {{item.label}}</p>
              </div>
              <div class="col-md-8">
                <div class="article-info">{{item.test}}</div>
                <p class="pull-right text-right readMore">
                  <a href="">Read More...</a>
                </p>
              </div>
            </div>
            <div class="nogutter"></div>
          </div>
        </div>
        <div class="pagination">
          <a href="" class="float-left">← Newer Posts</a>
          <a href="" class="float-right">Older Posts →</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'homeArticle',
  props: {
    article: Array
  }

}
</script>
<style scoped lang="less">
  .article{
    background-color:white;
    position:relative;
    padding-top:60px;
    z-index: 4;
    .pagination{
      display: block;
      padding-left: 0;
      margin: 20px 0;
      border-radius: 4px;
      overflow: hidden;
      a{
        color: #151515;
        text-decoration: none;
      }
    }
    .pagination::after{
      clear:both;
    }
    .article-title{
      font-style: normal;
      font-weight: 400;
      line-height: 1.6;
      font-size: 18px;
      margin-top: 0;
      margin-bottom:15px;
    }
    .article-time{
      margin-bottom:30px;
    }
    .article-text{
      color:#666;
    }
    .article-info{
      line-height:1.8;
      color:#666;
      margin-bottom:30px;
    }
    .readMore a{
      font-size: 12px;
      color: gray;
      padding-top: 2px;
      padding-bottom: 2px;
      padding-left: 10px;
      padding-right: 10px;
      border: 1px solid gray;
      border-radius: 20px;
    }
    .nogutter{
      height: 0;
      -webkit-box-sizing: content-box;
      -moz-box-sizing: content-box;
      box-sizing: content-box;
      border: 0;
      border-top: 1px solid #eee;
      margin-top: 40px;
      margin-bottom: 40px;
    }
  }
</style>



